<template>
    <form @submit.prevent="handleSubmit">
      <label>Title:</label>
      <input type="text" required v-model="title" />
      <label>Details:</label>
      <textarea v-model="details" required></textarea>
      <button>Update Project</button>
  </form>
</template>

<script>
export default {
    props: ['id'],
    data() {
        return {
            title: '',
            details: '',
            uri: '/js-elective/2nd-handin/db.json'
        }
    },
    mounted() {
        fetch(this.uri)
            .then(res => res.json())
            .then(data => {
                data = data.filter(project => project.id == this.id)[0]
                console.log(data)
                this.title = data.title,
                this.details = data.details
            })
            .catch(err => console.log(err.message))
    },
    methods: {
        handleSubmit() {
            fetch(this.uri, {
                method: 'PATCH',
                headers: { 'Content-Type' : 'application/json' },
                body: JSON.stringify({
                    id: this.id,
                    title: this.title,
                    details: this.details
                })
            })
            .then(() => {
                this.$router.push('/js-elective/2nd-handin/')
            })
            .catch(err => console.log(err.message))
        }
    }
}
</script>

